.faq-main {
  margin-bottom: 290px;

  @include md {
    margin-bottom: 170px;
  }

  @include sm-min {
    margin-bottom: 80px;
  }

  &__tabs {
    max-width: 800px;
    width: 100%;
    margin-right: 20px;
  }

  &__inner {
    display: flex;
    margin-bottom: 170px;

    @include md-min {
      flex-direction: column;
      align-items: center;
      margin-bottom: 80px;
    }
  }

  &__banner {
    background: url("../img/faq-banner.png") no-repeat center / cover;
    max-width: 400px;
    width: 100%;
    min-height: 450px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    &::after {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background-color: rgba(#ffffff, 0.2);
    }
  }
}

.faq-tab {
  overflow: hidden;
  &.opened {
    .faq-tab__trigger {
      svg {
        transition: transform 0.5s;
        transform: rotate(-180deg);
      }
    }

    .faq-tab__content {
      padding: 15px 20px;
      transition: all 0.5s;
    }
  }

  &__trigger {
    padding: 15px 40px;
    background: #ffffff;
    border: 1px solid #adb5bd;
    border-radius: 31px;
    font-family: "Rubik", sans-serif;
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    color: $black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 10;

    span {
      pointer-events: none;
    }

    svg {
      pointer-events: none;
      min-width: 11px;
      width: 11px;
      height: 17px;
      cursor: pointer;
      transition: transform 0.5s;
      margin-left: 10px;
    }
  }

  &__content {
    padding: 0 20px;
    overflow: hidden;
    transition: all 0.5s;
    pointer-events: none;
  }

  &:not(:last-child) {
    margin-bottom: 30px;
  }
}

.faq-info {
  position: relative;
  z-index: 10;
  &__title {
    font-family: "Rubik", sans-serif;
    font-weight: 400;
    font-size: 28px;
    line-height: 36px;
    color: $dark-green;
    max-width: 210px;
    text-align: center;
    margin-bottom: 70px;
  }
}

.faq-main-slider {
  position: relative;
  max-width: 1050px;
  margin: 0 auto;

  &__container {
    overflow: hidden;
    padding-bottom: 50px;
  }

  &__items {
    display: flex;
  }

  &__pag {
    .swiper-pagination-bullet {
      width: 12px;
      height: 12px;
      background: $light-green;
    }
  }
}

.faq-main-slide {
  width: 332px !important;
  height: 250px;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;

    @include sm {
      object-position: top;
    }

    @include xs {
      object-position: center;
    }
  }

  @include md-min {
    width: 50% !important;
  }

  @include sm {
    width: 100% !important;
    height: 350px;
  }

  @include xs {
    height: 200px;
  }
}
